.image-upload-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  overflow-y: auto;
}

.image-upload-modal-container {
  width: 100%;
  max-width: 500px;
  margin: auto;
}

.image-upload-modal-content {
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  width: 100%;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
}

.image-upload-modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid #e9ecef;
  flex-shrink: 0;
}

.image-upload-modal-header h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #212529;
  line-height: 1.4;
  flex: 1;
  text-align: center;
}

.image-upload-modal-close {
  background: transparent;
  border: none;
  font-size: 24px;
  line-height: 1;
  cursor: pointer;
  color: #6c757d;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
}

.image-upload-modal-close:hover {
  background: #f8f9fa;
  color: #212529;
}

.image-upload-back-btn {
  background: transparent;
  border: none;
  color: #6c757d;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  margin-right: 8px;
  transition: all 0.2s;
}

.image-upload-back-btn:hover {
  background: #f8f9fa;
  color: #212529;
}

.image-upload-modal-body {
  padding: 24px;
  flex: 1;
  overflow-y: auto;
  max-height: calc(90vh - 140px);
}

/*    */
.image-upload-modal-body::-webkit-scrollbar {
  width: 6px;
}

.image-upload-modal-body::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.image-upload-modal-body::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.image-upload-modal-body::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/*   */
.mobile-upload-hint {
  background: linear-gradient(135deg, #e3f2fd, #bbdefb);
  border-radius: 8px;
  padding: 12px 16px;
  margin: 0 24px 20px;
  display: flex;
  gap: 12px;
  align-items: flex-start;
}

.hint-content {
  flex: 1;
  text-align: left;
  font-size: 12px;
}

.hint-content strong {
  display: block;
  margin-bottom: 8px;
  color: #1565c0;
}

.hint-content span {
  margin: 0;
  color: #424242;
}

/*  */
.image-upload-form-group {
  margin-bottom: 24px;
}

.image-upload-form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #212529;
  font-size: 14px;
  text-align: start;
}

.image-upload-file-name-input,
.image-upload-article-input,
.image-upload-custom-tag-input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ced4da;
  border-radius: 6px;
  font-size: 14px;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  box-sizing: border-box;
}

.image-upload-file-name-input:focus,
.image-upload-article-input:focus,
.image-upload-custom-tag-input:focus {
  outline: 0;
  border-color: #4dabf7;
  box-shadow: 0 0 0 3px rgba(77, 171, 247, 0.15);
}

.image-upload-article-input.error {
  border-color: #fa5252;
}

.image-upload-article-input.error:focus {
  border-color: #fa5252;
  box-shadow: 0 0 0 3px rgba(250, 82, 82, 0.15);
}

.image-upload-input-hint {
  color: #6c757d;
  font-size: 12px;
  margin-top: 4px;
  padding-left: 8px;
  display: block;
  text-align: start;
}

/*   */
.char-counter {
  float: right;
  font-size: 12px;
  color: #6c757d;
  font-weight: normal;
}

/*  */
.image-upload-articles-container {
  margin-bottom: 12px;
}

.image-upload-article-input-wrapper {
  margin-bottom: 12px;
}

.image-upload-article-input-group {
  position: relative;
  width: 100%;
}

.image-upload-article-input {
  padding-right: 40px; /*     */
}

.image-upload-remove-article-btn {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  color: #adb5bd;
  cursor: pointer;
  font-size: 20px;
  line-height: 1;
  padding: 4px;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: all 0.2s;
}

.image-upload-remove-article-btn:hover {
  color: #fa5252;
  background: #fff5f5;
}

.image-upload-remove-article-btn span {
  display: block;
  line-height: 1;
}

.image-upload-error-message {
  color: #fa5252;
  font-size: 12px;
  margin-top: 4px;
  display: block;
  padding-left: 8px;
  text-align: start;
}

.image-upload-add-article-btn {
  background: transparent;
  border: 1px dashed #adb5bd;
  color: #495057;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  width: 100%;
  margin-top: 8px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.image-upload-add-article-btn:hover {
  background: #f8f9fa;
  border-color: #4dabf7;
  color: #4dabf7;
}

/*  */
.image-upload-tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 16px;
}

.image-upload-tag {
  padding: 6px 12px;
  border: 1px solid rgba(var(--tag-color-rgb, 102, 217, 232), 0.3);
  border-radius: 16px;
  background: rgba(var(--tag-color-rgb, 102, 217, 232), 0.1);
  color: #212529;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 4px;
  backdrop-filter: blur(4px);
  position: relative;
  z-index: 1;
}

.image-upload-tag:hover {
  background: rgba(var(--tag-color-rgb, 102, 217, 232), 0.2);
  border-color: rgba(var(--tag-color-rgb, 102, 217, 232), 0.5);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(var(--tag-color-rgb, 102, 217, 232), 0.2);
}

.image-upload-tag.selected {
  background: rgba(var(--tag-color-rgb, 102, 217, 232), 0.25);
  border-color: var(--tag-color, #06D6A0);
  color: #212529;
  font-weight: 600;
  box-shadow: 0 0 0 1px var(--tag-color, #06D6A0);
}

.image-upload-tag.selected::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border-radius: 18px;
  background: linear-gradient(
    45deg,
    var(--tag-color, #06D6A0),
    rgba(var(--tag-color-rgb, 102, 217, 232), 0.5)
  );
  z-index: -1;
  opacity: 0.3;
}

.tag-check {
  font-size: 12px;
  font-weight: bold;
}

/*   */
.image-upload-custom-tag-container {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
}

.image-upload-add-tag-btn {
  padding: 8px 16px;
  border: 1px solid #4dabf7;
  border-radius: 6px;
  background: #4dabf7;
  color: white;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
}

.image-upload-add-tag-btn:hover:not(:disabled) {
  background: #339af0;
  border-color: #339af0;
  transform: scale(1.05);
}

.image-upload-add-tag-btn:disabled {
  background: #e9ecef;
  border-color: #ced4da;
  color: #adb5bd;
  cursor: not-allowed;
  transform: none;
}

/*   */
.image-upload-selected-tags {
  border-top: 1px solid #e9ecef;
  padding-top: 16px;
  margin-top: 8px;
}

.selected-tags-label {
  font-size: 13px;
  color: #6c757d;
  margin-bottom: 8px;
  text-align: start;
}

.selected-tags-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.selected-tag-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 5px 10px 5px 12px;
  background: rgba(var(--selected-tag-color-rgb, 102, 217, 232), 0.15);
  border: 1px solid rgba(var(--selected-tag-color-rgb, 102, 217, 232), 0.3);
  border-radius: 16px;
  font-size: 13px;
  font-weight: 500;
  backdrop-filter: blur(4px);
  animation: tagAppear 0.3s ease-out;
}

@keyframes tagAppear {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.selected-tag-text {
  color: #212529;
}

.selected-tag-remove {
  background: transparent;
  border: none;
  color: rgba(var(--selected-tag-color-rgb, 102, 217, 232), 0.7);
  cursor: pointer;
  padding: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s;
  width: 18px;
  height: 18px;
}

.selected-tag-remove:hover {
  background: rgba(var(--selected-tag-color-rgb, 102, 217, 232), 0.2);
  color: #212529;
}

/*   -  */
.confirmation-step {
  padding: 8px 0;
}

/*   */
.image-preview-container {
  margin-bottom: 18px;
}

.image-preview-wrapper {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  background: #f8f9fa;
}

.image-preview {
  width: 100%;
  max-height: 200px;
  object-fit: contain;
  display: block;
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 12px 16px;
  background: linear-gradient(to bottom, rgba(0,0,0,0.7), transparent);
  color: white;
}

.image-badge {
  background: rgba(33, 150, 243, 0.9);
  color: white;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
}

/*    */
.file-info-section {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
}

.file-info-section h4 {
  margin: 0 0 16px 0;
  color: #212529;
  font-size: 16px;
  font-weight: 600;
  text-align: left;
}

.info-grid {
  display: grid;
  gap: 12px;
  margin-bottom: 20px;
}

.info-rows {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 8px;
  border-bottom: 1px solid #e9ecef;
}

.info-labels {
  font-size: 14px;
  color: #6c757d;
  font-weight: 500;
  width: 110px;
  text-align: left;
}

.info-values {
  font-size: 14px;
  color: #212529;
  font-weight: 500;
}

.info-values.final-name {
  color: #1976d2;
  font-weight: 600;
  word-break: break-all;
  text-align: right;
  max-width: 60%;
}

.file-name-final {
  font-family: monospace;
  background: #f1f3f4;
  padding: 4px 8px;
  border-radius: 4px;
  word-break: break-all;
  font-size: 13px;
  text-align: left;
}

/*    */
.tags-section {
  margin-bottom: 20px;
}

.tags-section h5 {
  margin: 0 0 8px 0;
  color: #6c757d;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
}

.tags-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.article-tag {
  background: #e3f2fd;
  color: #1565c0;
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 13px;
  font-weight: 500;
  border: 1px solid #bbdefb;
}

.tag-item {
  background: rgba(var(--tag-color-rgb, 102, 217, 232), 0.15);
  color: var(--tag-color, #06D6A0);
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 13px;
  font-weight: 500;
  border: 1px solid rgba(var(--tag-color-rgb, 102, 217, 232), 0.3);
}

/*  */
.author-row {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #e9ecef;
}

.author-icon {
  color: #6c757d;
  margin-right: 6px;
  vertical-align: -2px;
}

.author-name {
  font-size: 14px;
  color: #212529;
  font-weight: 500;
}

.upload-time {
  font-size: 12px;
  color: #6c757d;
}

/*    */
.image-upload-modal-footer {
  display: flex;
  justify-content: space-between;
  gap: 12px;
  padding: 20px 24px;
  border-top: 1px solid #e9ecef;
  flex-shrink: 0;
}

.image-upload-btn {
  padding: 10px 20px;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s;
  min-height: 40px;
  box-sizing: border-box;
}

.image-upload-btn-primary {
  background: #4dabf7;
  color: white;
  border: 1px solid #4dabf7;
}

.image-upload-btn-primary:hover:not(.disabled) {
  background: #339af0;
  border-color: #339af0;
}

.image-upload-btn-primary.disabled {
  background: #e9ecef;
  color: #adb5bd;
  border-color: #e9ecef;
  cursor: not-allowed;
  opacity: 0.7;
}

.image-upload-btn-secondary {
  background: white;
  color: #495057;
  border: 1px solid #ced4da;
}

.image-upload-btn-secondary:hover {
  background: #f8f9fa;
  border-color: #adb5bd;
}

.image-upload-btn-success {
  background: #28a745;
  color: white;
  border: 1px solid #28a745;
}

.image-upload-btn-success:hover:not(.disabled) {
  background: #218838;
  border-color: #1e7e34;
}

/*   */
.rotate-180 {
  transform: rotate(180deg);
}

/*  */
@media (max-width: 570px) {
  .image-upload-modal-overlay {
    padding: 10px;
  }
  
  .image-upload-modal-container {
    max-width: 100%;
  }
  
  .image-upload-modal-content {
    border-radius: 8px;
  }
  
  .image-upload-modal-header,
  .image-upload-modal-body,
  .image-upload-modal-footer {
    padding: 16px;
  }
  
  .mobile-upload-hint {
    margin: 0 16px 16px;
    padding: 10px 12px;
  }
  
  .file-info-section {
    padding: 16px;
  }
  
  .info-rows {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }
  
  .info-labels,
  .info-values {
    width: 100%;
  }
  
  .info-values.final-name,
  .file-name-final {
    max-width: 100%;
    text-align: center;
  }
  
  .image-upload-modal-footer {
    gap: 10px;
  }
  
  .image-upload-btn {
    width: 100%;
    justify-content: center;
  }
  
  .image-upload-tags-container {
    gap: 6px;
  }
  
  .image-upload-tag {
    padding: 5px 10px;
    font-size: 12px;
  }
  
  .image-upload-custom-tag-container {
    flex-direction: column;
  }
  
  .image-upload-add-tag-btn {
    width: 100%;
    padding: 10px;
  }
  
  .selected-tag-item {
    font-size: 12px;
    padding: 4px 8px 4px 10px;
  }
}